			type: String,
			default: 'Example title'
		}
	},
	data() {
		return {
			icon: mdiHome
		};
	},
	methods: {
		clickMethod(): void {
			/**
			 * Example comment for event. See comment for prop as to why this might seem over the top.
			 */
			this.$emit('click');
		}
	}
